"use client"

import { useState } from "react"
import { ChevronDown, X } from "lucide-react"
import { Link } from "react-router-dom"
import Header from "../../components/Header/Header"
import Footer from "../../components/Footer/Footer"
import "./TutorialPage.css"

export default function TutorialPage() {
    const [expandedStep, setExpandedStep] = useState(null)
    const [expandedImage, setExpandedImage] = useState(null)

    const steps = [
        {
            number: 1,
            title: "t thit b gn iPhone",
            description: "t thit b 'blinko' gn iPhone hoc cc thit b Apple khc.",
            images: [],
        },
        {
            number: 2,
            title: "Kim tra thng bo",
            description: "Kim tra v cho php ng dng 'Tm' thng bo.",
            images: ["/tutorials/1.png"],
        },
        {
            number: 3,
            title: "Bt Bluetooth v kt ni Internet",
            description: "Bt Bluetooth trn iPhone v kt ni n Internet.",
            images: ["/tutorials/2.png"],
        },
        {
            number: 4,
            title: "Rt dy cch in ca thit b",
            description: "Rt dy cch in ca 'blinko', thit b s bt ln cng vi ting bp.",
            images: ["/tutorials/3.png"],
        },
        {
            number: 5,
            title: "M ng dng Tm",
            description: "M ng dng 'Tm' trn iPhone ca bn.",
            images: ["/tutorials/4.png"],
        },
        {
            number: 6,
            title: "Thm vt dng",
            description:
                "Trong ng dng 'Tm', nhn vo 'Vt dng' tip theo nhn vo 'Thm vt dng' ri chn 'Vt dng c h tr khc'.",
            images: ["/tutorials/6.png", "/tutorials/7.png"],
        },
        {
            number: 7,
            title: "Tm kim v kt ni Siindoo Tag",
            description: "Tm kim module tag tn l 'siindoo Tag', nhn 'Kt ni'.",
            images: ["/tutorials/8.png"],
        },
        {
            number: 8,
            title: "t tn v chn biu tng",
            description: "t tn cho thit b 'blinko' v chn mt biu tng cm xc, sau  nhn 'Tip tc'.",
            images: ["/tutorials/9.png", "/tutorials/10.png"],
        },
        {
            number: 9,
            title: "Xc nhn thm vo Apple ID",
            description:
                "ng dng 'Tm kim ca ti' s yu cu xc nhn  thm thit b 'blinko' vo ti khon Apple ID ca bn. Nhn 'ng '.",
            images: [],
        },
        {
            number: 10,
            title: "Hon tt ci t",
            description: "Nhn 'Hon tt', v 'blinko' ca bn s c ci t v sn sng s dng.",
            images: ["/tutorials/11.png", "/tutorials/12.png"],
        },
    ]

    return (
        <div className="tutorial-page">
            {/* Header */}
            <Header />
            <header className="tutorial-header">
                <div className="tutorial-container">
                    <h1 className="tutorial-page-title">Hng dn s dng Blinko</h1>
                    <p className="tutorial-page-subtitle">Theo di 10 bc n gin  thit lp thit b Blinko cho b nha</p>
                </div>
            </header>

            {/* Main Content */}
            <main className="tutorial-main">
                <div className="tutorial-container">
                    {/* Steps Grid */}
                    <div className="steps-grid">
                        {steps.map((step) => (
                            <div key={step.number} className={`step-card ${expandedStep === step.number ? "expanded" : ""}`}>
                                {/* Step Header */}
                                <div
                                    className="step-header"
                                    onClick={() => setExpandedStep(expandedStep === step.number ? null : step.number)}
                                >
                                    <div className="step-number-badge">{step.number}</div>
                                    <div className="step-header-content">
                                        <h3 className="step-title">{step.title}</h3>
                                        <p className="step-preview">{step.description.substring(0, 60)}...</p>
                                    </div>
                                    <ChevronDown className={`step-chevron ${expandedStep === step.number ? "rotated" : ""}`} />
                                </div>

                                {/* Step Content */}
                                {expandedStep === step.number && (
                                    <div className="step-content">
                                        {step.images && step.images.length > 0 && (
                                            <div className="step-image-container">
                                                {step.images.map((image, idx) => (
                                                    <img
                                                        key={idx}
                                                        src={image || "/placeholder.svg"}
                                                        alt={`${step.title} - nh ${idx + 1}`}
                                                        className="step-image"
                                                        onClick={() => setExpandedImage(image)}
                                                        style={{ cursor: "pointer" }}
                                                    />
                                                ))}
                                            </div>
                                        )}
                                        <div className="step-description">
                                            <p>{step.description}</p>
                                        </div>
                                    </div>
                                )}
                            </div>
                        ))}
                    </div>

                    {/* Timeline View */}
                    <div className="timeline-section">
                        <h2 className="timeline-title">Quy trnh ci t</h2>
                        <div className="timeline">
                            {steps.map((step, index) => (
                                <div key={step.number} className="timeline-item">
                                    <div className="timeline-marker">
                                        <div className="timeline-dot">{step.number}</div>
                                        {index < steps.length - 1 && <div className="timeline-line"></div>}
                                    </div>
                                    <div className="timeline-content">
                                        <h4 className="timeline-step-title">{step.title}</h4>
                                        <p className="timeline-step-description">{step.description}</p>
                                        {step.images && step.images.length > 0 && (
                                            <div className="timeline-images">
                                                {step.images.map((image, idx) => (
                                                    <img
                                                        key={idx}
                                                        src={image || "/placeholder.svg"}
                                                        alt={`${step.title} - nh ${idx + 1}`}
                                                        className="timeline-image"
                                                        onClick={() => setExpandedImage(image)}
                                                    />
                                                ))}
                                            </div>
                                        )}
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>

                    {/* CTA Section */}
                    <div className="tutorial-cta">
                        <h2 className="cta-title">Sn sng bt u?</h2>
                        <p className="cta-description">
                            Nu bn gp bt k vn  no trong qu trnh ci t, vui lng lin h vi chng ti.
                        </p>
                        <div className="cta-buttons">
                            <Link to="/products" className="cta-button primary">
                                Mua Blinko ngay
                            </Link>
                            <a
                                href="https://www.facebook.com/profile.php?id=61581991141698"
                                target="_blank"
                                rel="noopener noreferrer"
                                className="cta-button secondary"
                            >
                                Lin h h tr
                            </a>
                        </div>
                    </div>
                </div>
            </main>

            {expandedImage && (
                <div className="image-lightbox" onClick={() => setExpandedImage(null)}>
                    <div className="lightbox-content" onClick={(e) => e.stopPropagation()}>
                        <button className="lightbox-close" onClick={() => setExpandedImage(null)}>
                            <X size={24} />
                        </button>
                        <img src={expandedImage || "/placeholder.svg"} alt="Expanded view" className="lightbox-image" />
                    </div>
                </div>
            )}

            <Footer />
        </div>
    )
}
